body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.lgin-envelop{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 90%;
  margin: 0 auto;
}

.login-envelop,
.signup-envelop{
  padding: 10%;
}

.form-envelop{
  max-width: 90%;
  margin: 0 auto;
}

.login-form-button,
.signup-form-button{
  width: 100%;
}

.container{
  padding: 25px;
}

.result{
  text-align: left;
  font-family: 'Courier New', Courier, monospace;
  overflow-wrap: break-word;
}

.itemResult{
  width: 10vw
}

.hexResult{
  overflow-wrap: break-word;
  width: 85vw;
  text-align: left;
}

.hide{
  display: none;
}

.input-file:hover{
  cursor: pointer;
}
